  beforeMount() {},
  mounted() {},
  methods: {
    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    robotCheckRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        // console.log(this.resolventList);

        // 表格是否为空(true不为空，false为空)
        var tag = false

        // 遍历表格列表，如果其中某项的title没有值时，则校验不能通过
        // console.log(this.tableData);
        if (!this.validateForm.robotList) {
          this.validateForm.robotList = []
        }
        this.validateForm.robotList.forEach((item) => {
          // console.log(item);
          if (name == '表格' && item) {
            if (!item.applicableModel) {
              tag = false // 表示有空值
              return callback(new Error('机器人型号不能为空'))
            } else if (!item.robotCount) {
              tag = false // 表示有空值
              return callback(new Error('机器人数量不能为空'))
            } else {
              if (isNaN(item.robotCount) || item.robotCount < 0) {
                return callback(new Error('机器人数量必须为正整数'))
              } else {
                tag = true
              }
            }
          }
        })
        // console.log(tag);
        if (name == '表格' && tag) {
          value = '这里随便给一个值就行' // 随便给一个值使验证通过
        }
        // console.log(value);
        // console.log(name);

        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    },

    // 获取搜索表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = {
        ...validateForm
      }
    },

    // 获取提交表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = {
        ...validateForm,
        robotList: []
      }
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 根据景区ID获取公司列表（搜索时公司列表）
     */
    async getCompanyList(spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get(
        '/system/subscriptionInformation/companyList',
        {
          params: {
            spotId: spotId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchCompanyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList(companyId) {
      if (!companyId) {
        companyId = null
      }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/factory_send/spot_list',
        {
          params: {
            companyId: companyId
          }
        }
      )
      // console.log(res);
      this.searchScenicOptions = res
      this.receivingOptions = res
      return res
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var beginDate = null
      var endDate = null

      if (this.searchStartEndDate.length !== 0) {
        beginDate = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endDate = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }

      this.tableLoading = true

      const { data: res } = await this.$http.get(
        '/system/factory_send/dispatchList',
        {
          params: {
            ...this.searchValidateForm,
            beginDate,
            endDate,
            pageNum: (this.currentPage - 1) * this.pageSize, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      // console.log(res);
      this.tableData = res.data
      this.total = res.total

      this.tableLoading = false
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
      const { data: res } = await this.$http.post('/system/factory_send/add', {
        ...this.validateForm,
        type: 1, // 默认待发货
        form: 2 // 1、工厂发货；2、景区调度
      })
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.$nextTick(() => {
          this.$refs.validateForm.resetFields() // 重置表单
        })
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 修改表格数据
     */
    async editTableData() {
      const { data: res } = await this.$http.post('/system/factory_send/edit', {
        ...this.editValidateForm,
        form: 2, // 1、工厂发货；2、景区调度
        id: this.currentEditRowId // 当前编辑行id
      })
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 设备调运详情信息
     */
    async detailTableDate(id) {
      const { data: res } = await this.$http.get(
        '/system/factory_send/detail',
        {
          params: {
            // form: 2, // 1、工厂发货；2、景区调度
            id: id // 当前编辑行id
          }
        }
      )
      var result = {}
      /**
       * 使用函数闭包特性，只需传入1次key值即可在内层函数中使用
       * 在这个函数中内层匿名函数与key变量共同组成了一个整体，构成闭包
       */
      function setResultProp(key) {
        return function(value) {
          if (value == '装车方式') {
            if (res.data[key] == 1) {
              result[value] = '人工装车'
            } else if (res.data[key] == 2) {
              result[value] = '叉车装车'
            } else {
              result[value] = ''
            }
          } else if (value == '发货进度') {
            if (res.data[key] == 1) {
              result[value] = '待发货'
            } else if (res.data[key] == 2) {
              result[value] = '已发货'
            } else if (res.data[key] == 3) {
              result[value] = '已收货'
            } else if (res.data[key] == 4) {
              result[value] = '已关闭'
            } else {
              result[value] = ''
            }
          } else {
            result[value] = res.data[key]
          }
        }
      }

      for (let key in res.data) {
        let f = setResultProp(key)
        switch (key) {
          case 'applicantName':
            f('申请人')
            break
          case 'applicantTime':
            f('申请时间')
            break
          case 'carType':
            f('货车类型')
            break
          case 'comsignmentNotes':
            f('发货备注')
            break
          case 'consigneeName':
            f('收货人')
            break
          case 'consigneePhone':
            f('收货人电话')
            break
          case 'consignmentCount':
            f('发货件数')
            break
          case 'consignmentDate':
            f('发货时间')
            break
          case 'consignmentPicture':
            f('发货图片')
            break
          case 'consignorName':
            f('发货人')
            break
          case 'driverName':
            f('驾驶员姓名')
            break
          case 'driverPhone':
            f('驾驶员电话')
            break
          case 'factoryName':
            f('发货工厂')
            break
          case 'license':
            f('车牌号')
            break
          case 'loadingCost':
            f('运费')
            break
          case 'loadingType':
            f('装车方式')
            break
          case 'receiningAddress':
            f('收货地址')
            break
          case 'receivingCount':
            f('收货数量')
            break
          case 'receivingDate':
            f('收货时间')
            break
          case 'receivingName':
            f('收货景区')
            break
          case 'receivingNotes':
            f('收货备注')
            break
          case 'receivingPicture':
            f('收货图片')
            break
          case 'receivingRobotCount':
            f('收货机器人数量')
            break
          case 'robotCodes':
            f('设备编码')
            break
          case 'applicableModel':
            f('机器人型号')
            break
          case 'robotCount':
            f('机器人数量')
            break
          case 'type':
            f('发货进度')
            break
          default:
            break
        }
      }

      this.detailTableList = result
    },

    /**
     * 获取工厂列表
     */
    async getFactoryList() {
      const { data: res } = await this.$http.get(
        '/system/factory_send/factory_list'
      )
