    async onChangeConsignee(consigneeId) {
      var consigneeName = ''
      this.consigneeOptions.forEach((item) => {
        if (item.userId == consigneeId) {
          consigneeName = item.userName
        }
      })
      this.validateForm.consigneeName = consigneeName
    },

    /** 提交表单中选项变化方法 end */

    // 日期选项值发生变化时触发
    onDateChange() {
      console.log(this.selectDateState)
      if (this.selectDateState == '1') {
        // 按月查询
        this.dateType = 'monthrange'
        this.valueFormat = 'yyyy-MM'
      } else if (this.selectDateState == '2') {
        // 按日查询
        this.dateType = 'daterange'
        this.valueFormat = 'yyyy-MM-dd'
      } else if (this.selectDateState == '3') {
        // 按年查询
        this.dateType = 'yearrange'
        this.valueFormat = 'yyyy'
      }
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.searchValidateForm.spotId = ''
      this.searchValidateForm.startEndDate = ''
      this.searchValidateForm.type = ''
      this.selectDateState = '1'
      this.dateType = 'monthrange'

      // 重新获取表格数据
      this.initTableData()
      // 重新获取搜索公司列表
      // this.getCompanyList()
      // 重新获取搜索景区列表
      // this.getScenicList()
    },

    // 点击查询数据按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.showTable = true
          // console.log(this.searchValidateForm);
          this.initTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true
          await this.getExportTableData()
          // 使用nextTick当table表格渲染完成后执行导出表格操作
          this.$nextTick(() => {
            var xlsxParam = { raw: true } //转换成excel时，使用原始的格式
            /* 从表生成工作簿对象 */
            var wb = XLSX.utils.table_to_book(
              document.querySelector('#outTable'),
              xlsxParam
            )
            /* 获取二进制字符串作为输出 */
            var wbout = XLSX.write(wb, {
              bookType: 'xlsx',
              bookSST: true,
              type: 'array'
            })
            try {
              FileSaver.saveAs(
                //Blob 对象表示一个不可变、原始数据的类文件对象。
                //Blob 表示的不一定是JavaScript原生格式的数据。
                //File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
                //返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
                new Blob([wbout], {
                  type: 'application/octet-stream'
                }),
                //设置导出文件名称
                '景区设备调运' + '.xlsx'
              )
            } catch (e) {
              if (typeof console !== 'undefined') console.log(e, wbout)
            }
            this.downloadLoading = false
            return wbout
          })

          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
      this.dialogState = 'add'
    },

    // 点击编辑时触发
    editRow(index, row) {
      console.log(index, row)

      if (row.type == 1) {
        this.currentEditRowId = row.id
        // 显示 dialog 弹窗
        this.editDialogVisible = true // 是否显示编辑dialog对话框
        this.dialogState = 'edit'

        var result = ''
        result = {
          type: row.type
        }

        // rowObj.paymentName = "张三" // 当前登录用户
        this.editValidateForm = result
      } else {
        // 提示用户不能编辑
        this.$message({
          message: '该行信息不能被编辑',
          type: 'warning',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    // 点击详情时触发
    detailRow(index, row) {
      // console.log(index, row);
      this.detailDialogVisible = true
      this.dialogState = 'detail'
      this.detailTableDate(row.id)
    },

    // 新增 dialog 取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑 dialog 取消
    editDialogClose() {
      this.editDialogVisible = false
      this.$refs.editValidateForm.resetFields() // 重置表单（清空无效）
      this.editValidateForm = {}
      // this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 详情 dialog 取消
    detailDialogClose() {
      this.detailDialogVisible = false
    },

    // dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑 dialog 右上角关闭按钮
    editHandleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.editValidateForm.resetFields() // 重置表单（清空无效）
      this.editValidateForm = {}
    },

    // 详情 dialog 右上角关闭按钮
    detailHandleClose(done) {
      done()
    },

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.dialogState == 'add') {
            // 新增工厂发货信息
            console.log('新增公司信息')
            this.addTableData()
            this.dialogVisible = false
          } else if (this.dialogState == 'edit') {
            // 编辑工厂发货信息
            console.log('编辑公司信息')
            this.editTableData()
            this.editDialogVisible = false
          } else if (this.dialogState == 'detail') {
            // 工厂发货详情信息
            console.log('工厂发货详情信息')
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    /**
     * 更新upload组件的方法
     */
    reloadUpload() {
      // this.$forceUpdate()
      // 移除组件
      this.updateUpload = false
      // 在组件移除后，重新渲染组件
      // this.$nextTick可实现在DOM 状态更新后，执行传入的方法。
      this.$nextTick(() => {
        this.updateUpload = true
      })
    },

    // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
    handleChange(file, fileList) {
      // console.log(file);
      this.fileListUpload[0] = file
      this.fileTemp = file.raw

      this.reloadUpload()

      // 导入判断
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
          this.fileTemp.type == 'application/vnd.ms-excel'
        ) {
          this.importfxx(this.fileTemp)
        } else {
          this.$message({
            type: 'warning',
            message: '附件格式错误，请删除后重新上传！',
            offset: 100,
            center: true,
            duration: 3000
          })
        }
      } else {
        this.$message({
          type: 'warning',
          message: '请上传附件！',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    // 文件超出个数限制时的钩子
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      )
    },

    // 文件列表移除文件时的钩子
    handleRemove(file, fileList) {
      this.fileTemp = null
      this.fileListUpload = []
    },

    // 导入格式
    importfxx(obj) {
      let _this = this
      // 通过DOM取文件数据
      this.file = obj
      var rABS = false //是否将文件读取为二进制字符串
      var f = this.file
      var reader = new FileReader()
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = ''
        var rABS = false //是否将文件读取为二进制字符串
        var pt = this
        var wb //读取完成的数据
        var outdata
        var reader = new FileReader()
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result)
          var length = bytes.byteLength
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i])
          }
          var XLSX = require('xlsx')
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //手动转化
              type: 'base64'
            })
          } else {
            wb = XLSX.read(binary, {
              type: 'binary'
            })
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]) //outdata就是你想要的东西
          console.log(outdata)
          this.da = [...outdata]
          let arr = []
          this.da.map((v) => {
            let obj = {
              robotCount: v['机器人数量'],
              type: v['发货进度'],
              applicantName: v['申请人'],
              factoryName: v['发货工厂'],
              consignorName: v['发货人'],
              receivingName: v['收货景区'],
              consigneeName: v['收货人'],
              consigneePhone: v['收货人电话'],
              receiningAddress: v['收货地址'],
              consignmentDate: v['发货时间']
            }
            arr.push(obj)
          })
          // // 将 arr 赋值给 tableData，来更新表格数据
          // this.tableData = arr
          return arr
        }
        reader.readAsArrayBuffer(f)
      }

      if (rABS) {
        reader.readAsArrayBuffer(f)
      } else {
        reader.readAsBinaryString(f)
      }
    },

    // 点击删除机器人表格行时触发
    delectOperatorRow(index, row) {
      this.validateForm.robotList.splice(index, 1)
      // 获取焦点再失去焦点，实现使校验提示消失的效果
      // this.$refs.tableInputRef[0]表示找到第一个input输入框
      // console.log(this.$refs.tableInputRef)
      this.$refs.robotTableInputRef[0].focus()
      this.$refs.robotTableInputRef[0].blur()
    },

    // 点击添加一行机器人信息
    onAddOperatorTable() {
      this.validateForm.robotList.push({
        applicableModel: '',
        robotCount: ''
      })
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
        // console.log(row, value);
        var result = ''
        if (value == 'type') {
          if (row[value] == 1) {
            result = '待发货'
          } else if (row[value] == 2) {
            result = '已发货'
          } else if (row[value] == 3) {
            result = '已收货'
          } else if (row[value] == 4) {
            result = '已关闭'
          }
        } else {
          result = row[value]
        }
        return result
      }
    }
  }
}
</script>

